import cityDatas from '../../components/city.area.js'
import uniPopup from '@/components/uni-pop.vue'
var province=""
var city=""
var area=""
var token=""
export default {
	components: {
				uniPopup
			},
	data() {
		return {
			provinceData: cityDatas,
			cityData: [],
			areaData: [],
			selectList: [{
			txt: '请选择'
			}, {
			txt: '请选择'
			}, {
			txt: '请选择'
			}],
			tabOne: '请选择',
			indexTab: 0,
			proviceShow: true,
			areaShow: false,
			cityShow: false,
			show: false,
			type: '',
			checkOne: null,
			checkTwo: null,
			checkThree: null,
			addressData: {
				id:"",
				nickname: '',
				phone: '',
				addressName: '点击选择省/直辖市',
				address: '',
				isDefault: false,
				postcode:""
			}
		}
	},
	onLoad(option){
		token = uni.getStorageSync("token")
		let title = '新增收货地址';
		if(option.type==='edit'){
			title = '编辑收货地址'
			this.loadAdrDetail(option.data)
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {
		switchChange(e){
			this.addressData.default = e.detail.value;
		},
		
		//地图选择地址
		chooseLocation(){
			uni.chooseLocation({
				success: (data)=> {
					this.addressData.addressName = data.name;
					this.addressData.address = data.name;
				}
			})
		},
		
		//提交
		confirm(){
			let data = this.addressData;
			if(!data.nickname){
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.phone)){
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			if(!data.address){
				this.$api.msg('请输入地址');
				return;
			}
			if(!data.postcode){
				this.$api.msg('请输入邮政编码');
				return;
			}
			if(!area||!city||!province){
				this.$api.msg('请选择省市区');
				return;
			}
			let _this = this;
			uni.request({
			    url:this.$url+'/user/address/add' ,//服务器端地址
			    data: {
					address:data.address,
					area:area,
					city:city,
					province:province,
					nickname:data.nickname,
					phone:data.phone,
					postcode:data.postcode,
					isDefault:data.isDefault,
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						this.$api.msg('添加成功');
			            uni.navigateBack({
			            	
			            })
			        }
			    }
			   
			}); 
		
		},
		loadAdrDetail(adrId)
		{
			let _this = this;
			uni.request({
			    url:this.$url+'/user/address/detail/'+adrId ,//服务器端地址
			    data: {
					
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        _this.addressData = res.data.data
					province = _this.addressData.province
					city = _this.addressData.city
					area = _this.addressData.area
					_this.selectList=[{txt:province},{txt:city},{txt:area}]
			    }
			   
			}); 
		},
		togglePopup(type, open) {
							this.type = type
							if (open === 'tip') {
								this.show = true
							} else {
								this.$refs[open].open()
							}
						},
						cancel(type) {
							if (type === 'tip') {
								this.show = false
								return
							}
							this.$refs[type].close()
						},
						change(e) {
							if (e.show == true) {
								uni.hideTabBar()
							} else {
								uni.showTabBar()
							}
						},
						tabEvent(index) {
							this.indexTab = index
							if (this.indexTab == 0) {
								this.proviceShow = true
								this.cityShow = false
								this.areaShow = false
								// this.checkOne = null
								this.checkTwo = null
								this.checkThree = null
								// this.cityData = []
								this.areaData = []
								// this.selectList[0].txt = "请选择"
								this.selectList[1].txt = "请选择"
								this.selectList[2].txt = "请选择"
							} else if (this.indexTab == 1) {
								this.proviceShow = false
								this.cityShow = true
								this.areaShow = false
								// this.checkTwo = null
								this.checkThree = null
								// this.areaData = []
								// this.selectList[1].txt = "请选择"
								this.selectList[2].txt = "请选择"
							} else if (this.indexTab == 2) {
								this.proviceShow = false
								this.cityShow = false
								this.areaShow = true
							}
						},
						provinceEvent(data, index) {
							this.checkOne = index
							this.selectList[0].txt = data.name
							this.indexTab = 1
							this.proviceShow = false
							this.cityShow = true
							this.areaShow = false
							this.cityData = data.cityList
							province = data.name
						},
						cityEvent(data, index) {
							this.checkTwo = index
							this.selectList[1].txt = data.name
							this.indexTab = 2
							this.proviceShow = false
							this.cityShow = false
							this.areaShow = true
							this.areaData = data.areaList
							city = data.name
						},
						areaEvent(data, index) {
							this.checkThree = index
							this.selectList[2].txt = data.name
							area = data.name
							this.cancel('popup')
						}
			
		}
	}
